@import url(https://fonts.googleapis.com/css2?family=Bungee+Shade&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee+Shade&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxanium&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxanium&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxanium&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxanium&display=swap);
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "Bungee Shade", cursive;
}

.navbuttons {
  font-size: 30px;
  width: 170px;
}

a {
  text-decoration: none;
  color: red;
  padding: 10px;
  transition: background-color 1s;
  border-radius: 10%;
}

a:hover {
  border-radius: 10%;
  background-color: red;
  color: white;
}

.heading {
  text-align: center;
  font-family: "Bungee Shade", cursive;
}

.red-part {
  color: red;
}

.green-part {
  color: green;
}

.blue-part {
  color: blue;
}

.home-page {
  display: flex;
  justify-content: center;
}

.home-page-picture {
  max-width: 100%;
  height: auto;
}

.comic {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 400px;
  width: 200px;
  font-family: "Oxanium", cursive;
}

.comic:hover {
  opacity: 1;
}

.comic-image > img {
  max-width: 100%;
  height: auto;
  border-radius: 10%;
}

.comic-name {
  font-weight: bold;
}

.add-to-cart {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.quantity > input {
  width: 30px;
  margin-right: 10px;
}

.add-to-chart-button {
  border: none;
  padding: 10px;
  border-radius: 5%;
  background-color: rgb(77, 77, 167);
  color: white;
  transition: background-color 0.5s;
  font-family: "Oxanium", cursive;
}

.add-to-chart-button:hover {
  background-color: rgb(17, 17, 175);
}

.items {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  font-family: "Oxanium", cursive;
}

.cart-item {
  display: grid;
  grid-template-rows: 1f;
  grid-template-columns: repeat(6, 1fr);
  justify-items: center;
  align-items: center;
  width: 800px;
  font-family: "Oxanium", cursive;
}

.cart-item-image {
  max-width: 100%;
  height: 100px;
  border-radius: 10%;
}

.cart-item-name {
  font-weight: bold;
}

.cart-item-quantity {
  width: 40px;
}

.remove-button {
  border: none;
  padding: 10px;
  background-color: rgb(167, 103, 77);
  color: white;
  transition: background-color 0.5s;
  border-radius: 5%;
  font-family: "Oxanium", cursive;
}

.remove-button:hover {
  background-color: rgb(158, 55, 14);
}

.cart-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Oxanium", cursive;
}

.complete-order {
  margin-top: 30px;
  display: grid;
  grid-template-rows: 1f;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
  width: 600px;
  padding: 10px;
}

.complete-order-button {
  border: none;
  padding: 10px;
  border-radius: 5%;
  background-color: rgb(77, 167, 125);
  color: white;
  transition: background-color 0.5s;
  font-family: "Oxanium", cursive;
}

.complete-order-button:hover {
  background-color: rgb(18, 131, 78);
}

span {
  font-weight: bold;
}

.order-message {
  font-size: 50px;
  text-align: center;
  font-family: "Oxanium", cursive;
}

.app {
  display: grid;
  grid-template-rows: 10% 20% 70%;
  height: 800px;
}


